
.option-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #F5F5F5;
}
.option-container .option-title {
    width: 100%;
    height: 150px;
    line-height: 150px;
    text-align: center;
    font-size: 50px;
    font-weight: 200;
    color: #CFCFCF;
}
.option-container .option-wrapper {
    width: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.option-container .each_group .weui-input {
    color: #fff;
}
.option-container .each_group .weui-input::-webkit-input-placeholder {
      color: #fff;
}
.option-container .each_group .weui-input::placeholder {
      color: #fff;
}
.option-container .each_group .weui-input::-webkit-input-placeholder {
      color: #fff;
}
.option-container .each_group .weui-cells {
    margin-top: 0;
    background: -webkit-linear-gradient(left top, #b08c58, #e4c998);
}
.option-container .each_group .weui-cells .weui-label {
      color: #fff;
      font-weight: 300;
}
.option-container .each_group .weui-cells .vux-popup-picker-placeholder {
      color: #fff;
      font-weight: 300;
}
.option-container .each_group .weui-cells .vux-cell-value {
      color: #fff;
}
.option-container .each_group .weui-cells .weui-cell__bd {
      padding-right: 11px;
}
.option-container .captain-search-container {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    margin-top: 5px;
    width: 100%;
    max-height: 120px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.option-container .captain-search-container .weui-cells {
      margin-top: 0;
}
.option-container .each_select {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    border-radius: 0;
}

.vux-fade-enter-active,
.vux-fade-leave-active {
  opacity: 1;
  -webkit-transition: opacity linear 0.2s;
  transition: opacity linear 0.2s;
}
.vux-fade-enter,
.vux-fade-leave-to {
  opacity: 0;
}
.vux-dialog-enter-active {
  -webkit-animation: vux-dialog-in 0.5s;
          animation: vux-dialog-in 0.5s;
}
.vux-dialog-leave-active {
  -webkit-animation: vux-dialog-out 0.3s;
          animation: vux-dialog-out 0.3s;
}
@-webkit-keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@keyframes vux-dialog-in {
0% {
    -webkit-transform: scale(1.185);
            transform: scale(1.185);
    opacity: 0;
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
}
@-webkit-keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
@keyframes vux-dialog-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
100% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
    opacity: 0;
}
}
.vux-mask-enter,
.vux-mask-leave-active {
  opacity: 0;
}
.vux-mask-leave-active,
.vux-mask-enter-active {
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
